.app__portfolio {
  flex: 1;
  width: 100%;
  flex-direction: column;
  padding: 2rem 0rem 0rem;

  @media screen and (min-width: 1200px) {
    padding: 5rem 0rem 0rem;
  }
}

.app__portfolio-filter {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  margin: 4rem 0 2rem;

  .app__portfolio-filter-item {
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    background-color: var(--teal-color);
    color: var(--black-color);
    font-weight: 800;

    cursor: pointer;
    transition: all 0.3s ease;
    margin: 0.5rem;

    &:hover {
      color: var(--white-color);
      text-shadow: 0 0 5px var(--white-color), 0 0 9px var(--white-color),
        0 0 15px var(--white-color), 0 0 23px var(--teal-color),
        0 0 38px var(--teal-color), 0 0 52px var(--teal-color),
        0 0 79px var(--teal-color), 0 0 100px var(--teal-color);
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
      border-radius: 0.85rem;
    }
  }

  .item-active {
    background-color: var(--secondary-color);
    color: var(--black-color);
    box-shadow: 0 0 6px var(--white-color);
  }
}

.app__portfolio-portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .app__portfolio-item {
    width: 270px;
    flex-direction: column;

    margin: 2rem 1rem;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: var(--gray-color);
    box-shadow: 0 0 35px rgba(0, 0, 0, 1);

    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
      background-color: var(--secondary-color);
      box-shadow: 0 0 35px rgba(255, 255, 255, 0.3);
    }

    @media screen and (min-width: 2000px) {
      width: 470px;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    @media screen and (max-width: 300px) {
      width: 100%;
      margin: 1rem;
    }
  }
}

.app__portfolio-img {
  width: 100%;
  height: 230px;

  position: relative;

  img {
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.app__portfolio-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--white-color);

    margin: 1rem;
    font-family: var(--font-base);
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3s ease;

    svg {
      width: 50%;
      height: 50%;
      color: var(--teal-color);
    }
  }
}

.app__portfolio-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  h4 {
    margin-top: 1rem;
    line-height: 1.5;
    color: var(--black-color);

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .p-text {
    color: var(--black-color);
  }

  .app__portfolio-tag {
    position: absolute;

    padding: 0.5rem 1rem;
    border-radius: 10px;
    background-color: var(--teal-color);
    top: -25px;

    .p-text {
      color: var(--black-color);

      &:hover {
        color: var(--white-color);
        text-shadow: 0 0 5px var(--white-color), 0 0 9px var(--white-color),
          0 0 15px var(--white-color), 0 0 23px var(--teal-color),
          0 0 38px var(--teal-color), 0 0 52px var(--teal-color),
          0 0 79px var(--teal-color), 0 0 100px var(--teal-color);
      }
    }
  }
}
