#home {
  position: relative;
  background: url("../../assets/brick.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: repeat;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 8rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 9rem;
  }

  @media screen and (max-width: 1200px) {
    padding-top: 6rem;
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  margin: 3rem 0 2rem 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp p {
    color: var(--teal-color);
  }

  .badge-cmp h1 {
    font-family: var(--font-accent);
    font-weight: 500;
    color: var(--white-color);
    text-shadow: 0 0 2px var(--white-color), 0 0 3px var(--white-color),
      0 0 5px var(--white-color), 0 0 12px var(--teal-color),
      0 0 17px var(--teal-color), 0 0 21px var(--teal-color),
      0 0 27px var(--teal-color), 0 0 37px var(--teal-color);
  }

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    background: rgba(14, 6, 12, 0.7);
    border-radius: 15px;
    flex-direction: row;
    width: auto;

    box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.9);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;

    @media screen and (max-width: 800px) {
      margin-top: 1rem;
    }

    p {
      width: 100%;
      color: var(--teal-color);
      text-transform: uppercase;
      text-align: right;
    }
  }

  span {
    font-size: 2.2rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: space-evenly;
    flex-direction: row;
    align-items: center;
  }

  @media screen and (max-width: 800px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 2rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: rgba(14, 6, 12, 0.7);
    box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.9);

    img {
      width: 60%;
      height: 60%;
    }
  }

  // circles with different height & width

  div:nth-child(1) {
    width: 105px;
    height: 105px;
  }

  div:nth-child(2) {
    margin: 1.75rem;
    width: 150px;
    height: 150px;
  }

  div:nth-child(3) {
    width: 75px;
    height: 75px;
  }

  div:nth-child(4) {
    margin: 1.75rem;
    width: 140px;
    height: 140px;
  }

  div:nth-child(5) {
    width: 75px;
    height: 75px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(2) {
      width: 400px;
      height: 400px;
    }

    div:nth-child(3) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
    div:nth-child(4) {
      width: 390px;
      height: 390px;
    }

    div:nth-child(5) {
      width: 150px;
      height: 150px;
    }
  }

  @media screen and (max-width: 800px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }

  @media screen and (max-width: 800px) {
    div:nth-child(2) {
      width: 125px;
      height: 125px;
    }

    div:nth-child(3) {
      width: 70px;
      height: 70px;
    }

    div:nth-child(1) {
      width: 100px;
      height: 100px;
    }
  }
}

.app__header-img {
  flex: 1;
  height: auto;

  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;

  img {
    width: 100%;
    border-radius: 50%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0px 0px 24px rgba(255, 255, 255, 0.1);
  }

  @media screen and (max-width: 1200px) {
    margin: 1rem 0;
  }
}
